import {
  CURRENT_CHAT,
  LOAD_CHAT_INFO,
  LOAD_NEW_TIKET,
  LOAD_MY_OPEN_TIKET,
  LOAD_USER_DATA,
  LOAD_CAT,
  ADD_MSG,
  ADD_CURRENT_CHAT
  
} from "../actions/Types";



const AppReducer = (state, action) => {
  switch (action.type) {

    case ADD_CURRENT_CHAT:
      return {
        ...state,
        currntChat: action.payload,
      };

    case ADD_MSG:
      return {
        ...state,
        msg: action.payload,
      };
    case LOAD_MY_OPEN_TIKET:
      return {
        ...state,
        pastTiket: action.payload,
      };

    case LOAD_NEW_TIKET:
      return {
        ...state,
        newTiket: action.payload,
      };

    case CURRENT_CHAT:
      return {
        ...state,
        currntChat: action.payload,
      };

    case LOAD_USER_DATA:
      return {
        ...state,
        userData: action.payload,
      };

    case LOAD_CAT:
      return {
        ...state,
        cat: action.payload,
      };

    case LOAD_CHAT_INFO:
      return {
        ...state,
        chatInfo: action.payload,
      };
    default:
      break;
  }
};
export default AppReducer;
