import { useReducer, useContext, useEffect, useState } from "react";
import {
  SHOW_TICKETS,
  CLOSE_TICKET,
  NEW_MESSAGE_REMOT,
  NEW_MESSAGE,
  NEW_SESSION,
  NEW_TICKET,
  SELECT_TICKET,
  SHOW_MY_TICKETS,
  GET_TICKET,
  GET_MESSAGES,
} from "./eventType";
import { AppContext } from "../ContextApi/contexts/AppContext";
import AppReducer from "../ContextApi/reducers/AppReducer";
import { SocketContext } from "../ContextApi/contexts/socket";
import {
  addMsg,
  loadUserData,
  setCurrentChat,
} from "../ContextApi/actions/AppActions";

export const useStateManger = () => {
  const GenralState = {
    cat: [
      { name: "", id: 1 },
      { name: "", id: 2 },
    ],
    userData: {
      id: "",
      name: "",
      type: "",
    },
    msg: "",
    currntChat: { ticket: "", chat: [] },
    chatInfo: {},
    pastTiket: [],
    newTiket: [],
  };

  return useReducer(AppReducer, GenralState);
};

export const useGetState = () => {
  return useContext(AppContext);
};

export const useGetCat = () => {
  const Socket = useContext(SocketContext);

  const { state, dispatch } = useContext(AppContext);
  const [error, setErorr] = useState("");
  const [data, setdata] = useState("");
  const [loading, setloading] = useState(false);

  useEffect(() => {
    setloading(true);
  }, []);

  return {
    error: error,
    data: data,
    loading: loading,
  };
};

export const useSend = () => {
  const Socket = useContext(SocketContext);
  const [sent, setmsg] = useState(false);
  const [AllMsg, setAllMsg] = useState("");
  const { state, dispatch } = useGetState();
  const addWillcom = (wms) => {
    const updarems = { ...state.currntChat };
    updarems.chat.push({
      id: Math.floor(Math.random() * 100),
      msg: wms,
      sender: "1",
    });
    setCurrentChat(updarems, dispatch);
  };
  useEffect(() => {
    Socket.emit(GET_MESSAGES, { Ticket: state?.currntChat?.ticket }, (data) => {
      if (state.userData.role_id == 1 && data.length == 0) {
        setTimeout(() => {
          addWillcom(" ");
        }, 500);

        setTimeout(() => {
          addWillcom("  ");
        }, 600);
      } else {
        data.forEach((element) => {
          const updarems = { ...state.currntChat };
          updarems.chat.push({
            id: element._id,
            msg: element.Message,
            sender: element.sender,
          });
          setCurrentChat(updarems, dispatch);
        });
      }
    });
    Socket.on(NEW_MESSAGE_REMOT, (data) => {
      //  console.log(data)
      const updarems = { ...state.currntChat };
      // console.log(data)
      updarems.chat.push({
        id: data._id,
        msg: data.Message,
        sender: data.sender,
      });
      // console.log(updarems)
      setCurrentChat(updarems, dispatch);
    });
  }, []);

  const sendMsg = () => {
    addMsg("", dispatch);
    setmsg(false);
    setAllMsg({
      sender: state.userData.id,
      Message: state.msg,
      receiver: null,

      Ticket: state.currntChat.ticket,
    });

    Socket.emit(
      NEW_MESSAGE,
      {
        sender: state.userData.id,
        Message: state.msg,
        receiver: null,

        Ticket: state.currntChat.ticket,
      },
      (data) => {
        const updarems = { ...state.currntChat };
        updarems.chat.push({
          id: data._id,
          msg: data.Message,
          sender: data.sender,
        });
        setCurrentChat(updarems, dispatch);

        setmsg(true);
      }
    );
  };

  return {
    sendMsg: sendMsg,
    sent: sent,
    AllMsg: AllMsg,
  };
};

export const useLoadUserInfo = (token) => {
  const { state, dispatch } = useGetState();
  const Socket = useContext(SocketContext);
  const [status, setStatus] = useState(false);

  useEffect(() => {
    Socket.emit(NEW_SESSION, { token }, (data) => {
      loadUserData(data.user, dispatch);
    });
  }, []);
};

export const useNewTiket = () => {
  const { state, dispatch } = useGetState();
  const Socket = useContext(SocketContext);
  const [status, setStatus] = useState(false);

  const sendNewT = (id) => {
    Socket.emit(
      NEW_TICKET,
      { UserId: state.userData.id, catId: id, SupportId: null, IsOpen: true },
      (data) => {
        console.log(data);
        setCurrentChat({ ticket: data.ticket, chat: [] }, dispatch);
        if (data.status) setStatus(true);
      }
    );
  };

  return {
    sendNewT: sendNewT,
    status: status,
  };
};

export const useShowAllTiket = () => {
  const Socket = useContext(SocketContext);
  const { state, dispatch } = useGetState();
  const [tdata, setdata] = useState();
  const [select, setselect] = useState(false);

  const selectTicket = (ID) => {
    Socket.emit(SELECT_TICKET, { myid: state.userData.id, id: ID }, (data) => {
      if (data) {
        setselect(true);
        setCurrentChat({ ticket: ID, chat: [] }, dispatch);
      }
    });
  };

  useEffect(() => {
    Socket.emit(
      SHOW_TICKETS,

      (data) => {
        console.log(data)
        setdata(data);
      }
    );
  }, []);

  return {
    select: select,

    data: tdata,
    selectTicket: selectTicket,
  };
};

export const useShowAllMyTiket = () => {
  const Socket = useContext(SocketContext);
  const [data, setdata] = useState([]);
  const [select, setselect] = useState(false);
  const { state, dispatch } = useGetState();

  //

  const selectTicket = (ID) => {
    Socket.emit(GET_TICKET, { ID: ID }, (data) => {
      console.log(data);

      if (data) {
        setselect(true);
        setCurrentChat({ ticket: data.ticket, chat: [] }, dispatch);
      }
    });
  };

  useEffect(() => {
    Socket.emit(SHOW_MY_TICKETS, { ID: state.userData.id }, ({ data }) => {
      console.log(data);
      setdata(data);
    });
  }, []);

  return {
    mytik: data,
    select: select,
    selectTicket: selectTicket,
  };
};

