import {
  CURRENT_CHAT,
  LOAD_CHAT_INFO,
  LOAD_NEW_TIKET,
  LOAD_MY_OPEN_TIKET,
  LOAD_USER_DATA,
  LOAD_CAT,
  ADD_MSG,
  ADD_CURRENT_CHAT
} from "./Types";

export const setCurrentChat = (state, dispatch) => {
  dispatch({ payload: state, type: ADD_CURRENT_CHAT  });
};
export const addMsg = (state, dispatch) => {
  dispatch({ payload: state, type: ADD_MSG  });
};

export const loadUserData = (state, dispatch) => {
  dispatch({ payload: state, type: LOAD_USER_DATA  });
};

export const loadChat = (state, dispatch) => {
  dispatch({ payload: state, type: CURRENT_CHAT });
};

export const getNewTiket = (state, dispatch) => {
  dispatch({ payload: state, type: LOAD_NEW_TIKET });
};

export const loadMyPastTiket = (state, dispatch) => {
  dispatch({ payload: state, type: LOAD_MY_OPEN_TIKET });
};

export const loadChatInfo = (state, dispatch) => {
  dispatch({ payload: state, type: LOAD_CHAT_INFO });
};

export const loadcat = (state, dispatch) => {
  dispatch({ payload: state, type: LOAD_CAT });
};
