import { SocketContext, Socket } from "./ContextApi/contexts/socket";
import { useStateManger } from "./utils/stateManger";
import { BrowserRouter, Routes, Route } from "react-router-dom";
import { AppContext } from "./ContextApi/contexts/AppContext";
import AllTikets from "./page/AllTikets";
import AllOpenTiket from "./page/AllOpenTiket";
import Home from "./page/Home";
import Chat from "./page/Chat";


function App() {
  const [state, dispatch] = useStateManger();
  
  return (
    <AppContext.Provider value={{ state, dispatch }}>
      <SocketContext.Provider value={Socket}>

       <BrowserRouter>
        <Routes>
        <Route path="/:token" element={<Home />}></Route>
        <Route path="/AllOpenTiket" element={<AllOpenTiket />}></Route>
        <Route path="/AllTikets" element={<AllTikets />}></Route>
        <Route path="/chat" element={<Chat />}></Route>
    
          </Routes>
          </BrowserRouter>
      </SocketContext.Provider>
    </AppContext.Provider>
  );
}

export default App;
