import {
  Card,
  CardContent,
  ListItem,
  List,
  ListItemButton,
} from "@mui/material";
import { useEffect } from "react";
import { useNavigate } from "react-router-dom";
import Layout from "../components/layout";
import { useShowAllMyTiket } from "../utils/stateManger";
export default function AllTikets() {
  const { mytik, selectTicket, select } = useShowAllMyTiket();

  const navigate = useNavigate();

  useEffect(() => {
    if (select) {
      navigate("/Chat", {
        state: {
          tickt: false,
        },
      });
    }
  }, [select]);
  return (
    <Layout>
      <Card>
        <CardContent>
          <List
            sx={{
              width: "100%",
              bgcolor: "background.paper",
              position: "relative",
              overflow: "auto",
              maxHeight: 350,
              "& ul": { padding: 0 },
            }}
          >
            {mytik &&
              mytik.reverse().map((item) => (
                <ListItem key={item._id} disablePadding>
                  <ListItemButton
                  disabled={!item.IsOpen}
                    onClick={() => selectTicket(item._id)}
                  >
                     : {item._id.substr(item._id.length - 5)}
                    {!item.IsOpen && "   "}
                  </ListItemButton>
                </ListItem>
              ))}
          </List>
        </CardContent>
      </Card>
    </Layout>
  );
}
