import { Box, Button } from "@mui/material";
import { useEffect } from "react";
import { useGetState, useNewTiket } from "../utils/stateManger";
import { useNavigate } from "react-router-dom";

export default function Categorys() {
  const { state } = useGetState();
  const { sendNewT, status } = useNewTiket();
  //const navigate = useLocation();
  const navigate = useNavigate();

  useEffect(() => {
    if (status) {

      
      navigate("/Chat", {
        state: {
          tickt: false
                },
      });

      
    };
    
  }, [status]);
  return (
    <Box>
      {state.cat.map((cat) => (
        <Button
          key={cat.id}
          onClick={() => sendNewT(cat.id)}
          sx={{ m: 1 }}
          variant="contained"
          size="medium"
        >
          {cat.name}
        </Button>
      ))}
    </Box>
  );
}
