import {
    Card,
    CardContent,
    ListItem,
    List,
    ListItemButton,
  } from "@mui/material";
  import { useEffect } from "react";
  import { useNavigate } from "react-router-dom";
  import Layout from "../components/layout";
  import { useShowAllMyTiket, useShowAllTiket } from "../utils/stateManger";
  export default function AllTikets() {

    const { data, selectTicket, select } = useShowAllTiket();
  
    const navigate = useNavigate();
  
    useEffect(() => {
      if (select) {
        navigate("/Chat", {
          state: {
            tickt: false,
          },
        });
      }
    }, [select]);

    return (
      <Layout>
        <Card>
          <CardContent>
            <List
              sx={{
                width: "100%",
                bgcolor: "background.paper",
                position: "relative",
                overflow: "auto",
                maxHeight: 350,
                "& ul": { padding: 0 },
              }}
            >
              {data?.length == 0 ? "  ":
            
                data?.reverse()?.map((item) => (
                  <ListItem key={item._id} disablePadding>
                    <ListItemButton
                    disabled={!item.IsOpen}
                      onClick={() => selectTicket(item._id)}
                    >
                       : {item._id.slice(0, 9)}
                      {!item.IsOpen && "   "}
                    </ListItemButton>
                  </ListItem>
                ))}
            </List>
          </CardContent>
        </Card>
      </Layout>
    );
  }
  