import {
  Card,
  CardContent,
  CardActions,
  IconButton,
  InputAdornment,
  Button,
  Stack,
} from "@mui/material";
import InputForm from "../components/InputForm";
import { IoSendSharp } from "react-icons/io5";
import Layout from "../components/layout";
import { useGetState, useSend , useCloseTiket} from "../utils/stateManger";
import { addMsg } from "../ContextApi/actions/AppActions";
import { useEffect } from "react";
import { useNavigate } from "react-router-dom";

export default function Chat() {
  const { state, dispatch } = useGetState();
  const { closeT, closed } = useCloseTiket();
  const { sendMsg, sent } = useSend();
  const navigate = useNavigate();
  
  useEffect(() => {
    //  navigate.state.tickit
    //  console.log(state.currntChat.chat);
  }, [sent]);


  useEffect(()=>{
    if(closed){
      navigate(`/${state.userData.role_id}`);
    }
  },[closed]) 
  

  return (
    <Layout>
      <Card>
        <CardContent>
          <Stack
            gap={1}
            sx={{ p: 0.5, minHeight: "350px", backgroundColor: "whitesmoke" }}
          >
            {state.currntChat.chat.map((msg) => (
              <Stack
                key={msg.id}
                direction={"row"}
                justifyContent={
                  state.userData?.id == msg.sender ? "flex-start" : "flex-end"
                }
              >
                <Card sx={{ padding: 1 }}>{msg.msg}</Card>
              </Stack>
            ))}
          </Stack>
          <CardActions>
            <InputForm
              // disabled={loading}
              onChange={(e) => addMsg(e.target.value, dispatch)}
              value={state.msg}
              label=""
              startAdornment={
                <InputAdornment position="start">
                  <IconButton onClick={() => sendMsg()} aria-label="send">
                    <IoSendSharp size={25} />
                  </IconButton>
                </InputAdornment>
              }

              //  onChange={(e) => addCountry(e.target.value, dispatch)}
            ></InputForm>
            {state.userData.role_id != 1 ? (
              <Button 
              onClick={()=>closeT()}
              variant="contained" color="error">
                 
              </Button>
            ) : (
              ""
            )}
          </CardActions>
        </CardContent>
      </Card>
    </Layout>
  );
}
